import { selectOption } from '../../lib/wohnungshelden/selectOption.js'
import { type } from '../../lib/wohnungshelden/type.js'

export async function fillForm ({ form, page }, contactData) {
  await selectOption(
    { form, page },
    'nz-select[formcontrolname="salutation"]',
    '#cdk-overlay-0 > div > div > ul',
    contactData.title === 'Mrs.' ? 0 : 1,
  )
  await type({ form, page }, '#firstName', contactData.firstName)
  await type({ form, page }, '#lastName', contactData.lastName)
  await type({ form, page }, '#email', contactData.email)
  if (contactData.phone) {
    await type({ form, page }, '#phone-number', contactData.phone)
  }
  await type({ form, page }, '#applicant-message', contactData.applicationMessage)
  await type(
    { form, page },
    '#formly_2_input_numberPersonsTotal_0',
    String(contactData.numberOfAdults + contactData.numberOfChildren),
  )
}
